
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  background-color:#ececeb;
  margin:0px;
  position:absolute;
  top:0px;
  left:0px;
  width:100%;
  height:100%;
}
#header {
  width:100%;
  height:80px;
  background-color:#B8DFF0;
  position:absolute;
  top:0px;
  left:0px;
  text-align: center;
  z-index:70;
}
#header h1 {
  font-size:32px;
  color:white;
}
#sidebar {
  position:absolute;
  top:0px;
  left:0px;
  width:250px;
  height:100%;
  background-color:white;
  display:block;
  z-index:50;
}
#sidebar ul{
  text-align:left;
  padding:80px 0px 0px 0px;
}
#sidebar ul li{
  list-style:none;
  padding:25px;
  font-size:18px;
  text-align:left;
  font-color:#ccc;
  margin:0px;
}
#sidebar ul li:hover{
  background-color:#bff4ed;
}
.buttons {
  position:absolute;
  right:5%;
  bottom:20px;
  text-align:center;
  z-index:200;
}
.add {
  width:70px;
  height:70px;
  border-radius:50%;
  background-color:#f9ce00;
  text-align:center;
  line-height:70px;
  font-size:50px;
  font-weight:10px;
  color:white;
  cursor:pointer;
}
#confirm {
  width:60px;
  height:60px;
  border-radius:50%;
  background-color:#7f7f7f;
  text-align:center;
  line-height:60px;
  font-size:18px;
  font-weight:10px;
  color:white;
  cursor:pointer;
  margin-bottom:40px;
}
#addInput {
  width:100%;
  height:100%;
  position:absolute;
  top:0px;
  left:0px;
  z-index:100;
}
#newItem {
  position:relative;
  left:5%;
  width:90%;
  height:70px;
  border:none;
  font-size:24px;
  top:120px;
  border-bottom:#aaa 3px solid;
  outline:none;
}
.todoLists,.doneLists {
  position:relative;
  top:90px;
}
.todoLists li,.doneLists li{
  position:relative;
  left:2%;
  width:90%;
  height:60px;
  line-height:60px;
  font-size:20px;
  background-color:white;
  padding:10px 20px; 
  list-style:none;
  margin:15px 0px;
}
.finished {
  color:#c1c1c1;
  text-decoration:line-through;
}
.delete,.complete {
  position:absolute;
  right:30px;
  font-size:16px;
  cursor:pointer;
}
.todoLists .delete {
  right:70px;
}
.doneLists .delete{
  right:30px;
}
.changeskin,.aboutme {
  position:absolute;
  top:160px;
  padding:30px 60px;
  background-color:white;
  width:80%;
  left:4%;
  font-size:18px;
  margin:0px;
}
.changeskin li {
  position:relative;
  list-style:none;
  width:80%;
  padding:15px 40px;
  margin:0 auto;
  cursor:pointer;
}
.changeskin li span {
  padding:0px 18px;
  margin:20px 0px;
  position:relative;
  left:70%;
}
.green{
  background-color:#61b292;
  border-radius:40% 0 0 40%;
}
.lightgreen{
  background-color:#c6f1e7;
  border-radius:0 40% 40% 0;
}
.red{
  background-color:#fa4659;
  border-radius:40% 0 0 40%;
}
.lightred{
  background-color:#ffe6eb;
  border-radius:0 40% 40% 0;
}
.blue{
  background-color:#11cbd7;
  border-radius:40% 0 0 40%;
}
.lightblue{
  background-color:#c7f3ff;
  border-radius:0 40% 40% 0;
}
.aboutme {
  text-align:center;
}
.aboutme a{
  text-decoration:none;
}
